@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?d1zxg5');
  src:  url('../fonts/icomoon.eot?d1zxg5#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?d1zxg5') format('truetype'),
    url('../fonts/icomoon.woff?d1zxg5') format('woff'),
    url('../fonts/icomoon.svg?d1zxg5#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-error:before {
  content: "\e900";
  color: #ff7979;
}
